    import headTop from '@/components/base/header/head'
    export default {
        components: {
            headTop
        },
        data() {
            return {
                info: "",  // 初始数据
                showtime: false,   // 显示自定义事件弹窗
                startTime: null,  //  开始时间
                dataTime: null,   //  初始时间
                value: 0,   //  默认显示昨天
            }
        },
        created(){
            this.dataTime = this.common.commonFun.get_date(0)
        },
        mounted(){
            this.getInfo();  //  初始化数据
        },
        methods:{
            getInfo(){
                this.info = [];
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.getMonitorStatistics,
                    type: "post",
                    data: {
                        time: this.dataTime
                    },
                    success: (response, allResponse) => {
                        console.log('response',response)
                        this.info = response
                    }
                });
            },
            //            自定义弹窗=>点击确定
            onConfirm(){
                this.dataTime = this.startTime;
                this.getInfo();
            },
//            选择时间
            date(index){
                this.value = index;
                if(index == 0){
                    this.dataTime = this.common.commonFun.get_date(0);
                    this.getInfo();
                }
                if(index == 1){
                    this.dataTime = this.common.commonFun.get_date(1);
                    this.getInfo();
                }
                if(index == 2){
                    this.showtime = true;
                }
            }
        }
    }



